#ph-buttons {
  position: relative;
  float: right;
  clear: right;
  overflow: hidden;
  margin: 10px 0;
  width: 100%;
}

.ph-edit-btn,.ph-btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  clear: none;
  margin: 0 0 0 20px;
  border: 1px solid;
  font-weight: 700;
  font-style: normal;
  padding: 10px 20px;
  text-align: center;
}

.ph-edit-btn i {
  display: inline-block;
  font-style: normal;
}

#ph-wrapper #ph-toggle-editor {
  /*margin: 0 20px 0 0;*/
  font-weight: 300;
  font-size: 15px;
  padding: 5px 10px;
  color: #454545;
  background-color: #FFFFFF;
}

#ph-wrapper #ph-toggle-editor:hover {
  color: #00B16A;
}

#ph-wrapper #ph-toggle-editor:active {
  color: #FFFFFF;
  background-color: #454545;
  /*border-color: #454545;*/
}

#ph-buttons #ph-create {
  float: right;
  border-color: transparent;
  background-color: #FFFFFF;
  color: #454545;
  position: relative;
  text-align: center;
  /*float: none;*/
  /*color: #FF9000;*/
  margin-right: 10px;
}

#ph-buttons #ph-create:active {
  border-color: #454545;
}

#ph-buttons #ph-save {
  float: right;
  color: #FFFFFF;
  background-color: #00B16A;
}

#ph-buttons #ph-save:active {
  color: #00B16A;
  border-color: #00B16A;
  background-color: #FFFFFF;
}

.fullPage .ph-edit-btn {
  display: none;
}

@media screen and (max-width: 768px) {
  #ph-wrapper #ph-toggle-editor {
    margin: 10px auto;
  }
}

#ph-wrapper .CodeMirror.CodeMirror {
  backface-visibility: hidden;
  perspective: 1000px;
  transform: translate3d(0, 0, 1px);
  position: absolute;
  border: 1px solid #CCCCCC;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
}

#ph-wrapper .CodeMirror.CodeMirror,#ph-wrapper #input textarea {
  font-family: "Ubuntu Mono", "UbuntuMono", "Lucida Console", Consolas, "Lucida Grande", "Liberation Mono", Menlo, Courier, monospace !important;
}

#ph-wrapper .CodeMirror pre {
  line-height: 1.5;
}

#ph-wrapper .CodeMirror-scroll {
  padding: 20px;
}

#ph-wrapper .cm-header-1 {
  font-size: 2em;
}

#ph-wrapper .cm-header-2 {
  font-size: 1.75em;
}

#ph-wrapper .cm-header-3 {
  font-size: 1.5em;
}

#ph-wrapper .cm-header-4 {
  font-size: 1.3em;
}

#ph-wrapper .cm-header-5 {
  font-size: 1.2em;
}

#ph-wrapper .cm-header-6 {
  font-size: 1.15em;
}

#ph-wrapper .cm-quote {
  color: #90A959;
  font-style: italic;
  margin: 0;
  padding-left: 3em;
  border-left: .5em #EEEEEE solid;
}

#ph-side-nav {
  backface-visibility: hidden;
  perspective: 1003px;
  transform: translate3d(0,0,0);
  opacity: 1;
}

#ph-nav {
  /*color: #FAFCFE;*/
  background-color: #0072C6;
  position: relative;
  float: left;
  clear: none;
  width: 320px;
  height: 100%;
  overflow: hidden;
  font-family: Roboto, "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  padding-top: 40px;
  padding-bottom: 40px;
  border-radius: 16px 0 0 16px;
}

#ph-nav .header,#ph-nav .logo,#ph-nav .text {
  display: block;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  line-height: normal;
}

#ph-nav .header {
  padding-top: 10px;
  padding-bottom: 10px;
}

#ph-nav .text {
  font-size: 21px;
}

#ph-nav .text small {
  font-size: .65em;
}

#ph-nav a {
  color: #FAFAFA;
}

#ph-nav a:hover > .link-title {
  text-decoration: underline;
}

#ph-nav a.active:hover > .link-title {
  text-decoration: none;
}

#ph-nav a.active:hover > .toggle-menu::before {
  border-color: #FFFFFF;
}

#ph-nav a.active:active > .toggle-menu::before {
  border-color: #DDDDDD;
  color: #DDDDDD;
}

#ph-nav .link .icon::before {
  margin: 0;
  position: absolute;
  /*top: 50%;
	transform: translateY(-50%);*/
}

