}

#ph-nav .ph-level-1 {
  color: #FFFFFF;
  font-size: 25px;
  margin: 0;
  line-height: 1.3;
  padding: 15px 20px 15px 40px;
}

#ph-nav .ph-level-2 {
  font-size: 15px;
  padding: 6px 45px 6px 20px;
  line-height: 1.3;
}

#ph-nav .ph-parent.ph-opened + .ph-page,#ph-nav .ph-parent.ph-opened ~ .ph-page + .ph-program,#ph-nav .ph-parent.ph-opened ~ .ph-rabbit-hole + .ph-program {
