#ph-nav .ph-level-3 {
  color: #FAFAFA;
  text-shadow: 0 1px 0 #222;
  font-size: 13px;
  line-height: 1.3;
  padding: 3px 30px 3px 60px;
}

#ph-nav .ph-rabbit-hole {
  list-style-type: none;
  text-align: left;
  padding: 8px 0;
  margin: 0 60px;
  width: calc(100% - 120px);
}

#ph-nav .ph-level-4 {
  color: #FFFFFF;
  font-size: 13px;
  margin: 0 auto;
  padding: 3px 5px;
  max-width: 85%;
