#ph-nav .ph-rabbit-hole .active .place,#ph-nav .ph-sub-parent .active .place {
  display: none;
}

#ph-nav .toggle-menu {
  position: absolute;
  right: 40px;
  color: #FFFFFF;
  vertical-align: middle;
  border: 1px solid transparent;
  line-height: 1.6;
}

#ph-nav .toggle-menu::before {
  width: 24px;
  height: 24px;
  text-align: center;
  vertical-align: middle;
  border-radius: 15px;
  font-weight: bold;
  border: 1px solid transparent;
}

#ph-nav .toggle-menu:hover::before {
  border-color: #FFFFFF;
}

#ph-nav .toggle-menu:active::before {
  color: #DDDDDD;
  border-color: #DDDDDD;
}

#ph-nav .ph-sub-parent {
  background: transparent;
  font-variant: small-caps;
}

#ph-nav .ph-sub-parent .active,#ph-nav .ph-sub-parent a.active:hover {
  text-decoration: underline;
}

