#ph-tabs {
  position: relative;
  overflow: hidden;
  margin: 50px auto 0;
  width: 60%;
  font-weight: 300;
  font-size: 1em;
  border-bottom: none;
}

.fullPage #ph-tabs {
  margin: 20px auto 0;
  width: 100%;
  font-size: 1.25em;
}

#ph-tabs nav {
  text-align: center;
  /*border-bottom: 1px solid #777777;*/
}

.fullPage #ph-tabs nav {
  /*border-bottom: none;*/
}

#ph-tabs nav ul {
  position: relative;
  display: flex;
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;
  -ms-box-orient: horizontal;
  -ms-box-pack: center;
  flex-flow: row wrap;
  justify-content: center;
}

#ph-tabs nav ul {
  max-width: 100%;
}

#ph-tabs nav ul li {
  position: relative;
  display: block;
  transform: translateZ(0);
  margin: 0;
  text-align: center;
  flex: 1;
}

#ph-tabs nav a {
  position: relative;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1.25;
}

#ph-tabs nav a span {
  vertical-align: middle;
  font-size: .6em;
}

/*#ph-tabs nav .ph-tab-current:not(.ph-tab-empty) .ph-tab-box {
	background-color: #777777;
}*/

.fullPage #ph-tabs nav .ph-tab-current .ph-tab-box {
  background-color: transparent;
}

/*#ph-tabs nav .ph-tab-current a {
	color: #FFFFFF;
}*/

.fullPage #ph-tabs nav .ph-tab-current a {
  color: #777777;
