/**
 * Another excellent loading animation courtesy of:
 * http://codepen.io/lesbaa/
 * Thanks!
 */

#ph-loader {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 300px;
  height: 300px;
}

.ph-loader {
  /*padding: 50px;*/
}

.loader-group {
  position: absolute;
  display: block;
  transform: translateX(-50%) translateY(-50%);
  top: 50%;
  left: 50%;
  line-height: 16px;
  margin: 30px auto;
  text-align: center;
  width: 80px;
}

.bigSqr {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  overflow: hidden;
  transform-origin: bottom left;
  animation: bigSqrShrink 1s linear infinite;
}

.square {
  position: absolute;
  width: 20px;
  height: 20px;
  background: #FF9900;
}

.first {
  left: 0;
  top: 20px;
}

.second {
  left: 20px;
  top: 20px;
  animation: drop2 1s linear infinite;
}

.third {
  left: 0;
  top: 0;
  animation: drop3 1s linear infinite;
}

.fourth {
  left: 20px;
  top: 0;
  animation: drop4 1s linear infinite;
}

.loader-group .text {
  line-height: 16px;
  color: #FF9E00;
  display: block;
  margin: 10px auto;
  padding: 3px;
}

@keyframes bigSqrShrink {
  0% {
    transform: scale(1);
  }

  90% {
    transform: scale(1);
  }

  100% {
    transform: scale(0.5);
  }
}

@keyframes drop2 {
  0% {
    transform: translateY(-50px);
  }

  25% {
    transform: translate(0);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes drop3 {
  0% {
    transform: translateY(-50px);
  }

  50% {
    transform: translate(0);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes drop4 {
  0% {
    transform: translateY(-50px);
  }

  75% {
    transform: translate(0);
  }

  100% {
    transform: translate(0);
  }
}

